






















import {Component, Prop, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import emailRegEx from "@/mixins/emailRegEx"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"
@Component({ components: { } })
export default class ForgotPassword extends Vue {
	private email: string = ""
	private password: string = ""

	private get isFormValid(): boolean {
		return this.isEmailValid && this.password.trim().length >= 8
	}

	private get isEmailValid(): boolean {
		return emailRegEx.test(this.email)
	}

	private submit(): void {
		Portal.client.call.account.requestResetPassword(this.email, this.password)
			.response
			.then(() => {
				this.close("Weâ€™ve emailed instructions to reset your password")
			})
	}

	private close(msg: string | null = null): void {
		this.$emit("close", msg)
	}
}
