



































import { Component, Vue } from "vue-property-decorator"
import {IPayment} from "@chaosinsight/yogipeople-portalclient"
import {IPaymentMethod} from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"
import Portal from "@/managers/portal"
import PageBackground from "@/components/PageBackground.vue"
import ForgotPassword from "@/components/ForgotPassword.vue"
import Profile from "@/managers/profile"
import NotificationMessage from "@/components/NotificationMessage.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({
	components: { ForgotPassword, NotificationMessage, PageBackground }
})
export default class TheLogin extends Vue {
	public email: string = ""
	public password: string = ""
	public notifyMsg: string | null = null
	public passwordReset: boolean = false
	public specificPageMsg: TranslateResult | null = null
	private emailLogin: string = ""
	private savedCards: IPaymentMethod[] | null = null
	private isEmailVerified: boolean | null = null
	public created(): void {
		// not authenticated before login
		Portal.isAuthenticated = false
		// Message to user - specific page
		if (this.$route.query.confirm)
			this.specificPageMsg = i18n.t("login.forgotPassword.emailToast")
			else if (this.$route.query.pass)
			this.specificPageMsg = i18n.t("login.login.specificMessage2")
			// Login after Create Account
		if (this.$route.params.email && this.$route.params.password) {
				this.email = this.$route.params.email
				this.password = this.$route.params.password
				this.login()
			}
		this.emailLogin = this.$route.fullPath.split("?")[1]
		if (this.emailLogin === "confirm=true")
			this.specificPageMsg = "Thanks Your email has been verified!"
		}
	public login(): void {
		Portal.login(this.email, this.password)
		.then(res => {
				window.localStorage.setItem("email", this.email)
				window.localStorage.setItem("pass", this.password)
				window.localStorage.setItem("Login", "Loggedin")
				if (Portal.isAuthenticated) {
					this.$router.push("/home")
					Portal.client.call.student.me()
					.response
					.then(reslt => {
					this.isEmailVerified = reslt.IsMobileVerified
					if (this.isEmailVerified)
						Portal.client.call.payments.paymentMethodsGet("card")
						.response
						.then(result => {
							this.savedCards = [].slice.call(result)
							// if (this.savedCards!.length === 0) {
							// this.$router.push("/card")
							// window.localStorage.setItem("fromlogin", "yes")
							// window.localStorage.setItem("paymentroute", "zero") } else
							this.$router.push("/home")
							})
					})
				}
			})
			.catch(reason => {
				this.notifyMsg = reason
				})
	}
	public get isFormValid(): boolean {
		return !!this.email && !!this.password
	}
	public closePasswordReset(msg: string | null) {
		this.passwordReset = false
		this.notifyMsg = msg
	}
	public goBack(): void {
		this.$router.push("/")
		}
}
